import { useState } from 'react';
import PropTypes from 'prop-types';
import {
  IconButton,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Typography
} from '@mui/material';
import MainCard from '../cards/MainCard';
import { Details, VoicemailRounded } from '@mui/icons-material';
import Image from 'mui-image';

const ProductListStock = ({ stocks, ...rest }) => {
  const [limit, setLimit] = useState(10);
  const [page, setPage] = useState(0);
  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  return (
    <MainCard {...rest}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              Name
            </TableCell>
            <TableCell>
              SKU
            </TableCell>
            <TableCell>
              Stock
            </TableCell>
            <TableCell>
              Price
            </TableCell>
            <TableCell>
              Actions
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {stocks.map((stock) => (
            <TableRow
              hover
              key={stock.id}
            // selected={selectedCustomerIds.indexOf(stock.id) !== -1}
            >
              <TableCell component="th" scope="row" padding="none">
                <Stack direction="row" alignItems="center" spacing={2}>
                  <Image src={stock.avatarUrl} shift="left" width={48} height={48} sx={{ display: { sm: 'none', lg: 'inline' } }} />
                  <Typography variant="subtitle2" noWrap>
                    {stock.name}
                  </Typography>
                </Stack>
              </TableCell>
              <TableCell>
                {stock.sku}
              </TableCell>
              {/* <TableCell>
                {format(Number(stock.createdAt), 'dd/MM/yyyy')}
              </TableCell> */}
              <TableCell>
                {stock.stock}
              </TableCell>
              <TableCell>
                {stock.price}
              </TableCell>
              <TableCell>
                <IconButton >
                  <Details />
                </IconButton>
                <IconButton >
                  <VoicemailRounded />
                </IconButton>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <TablePagination
        component="div"
        count={stocks.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </MainCard>
  );
};

ProductListStock.propTypes = {
  stocks: PropTypes.array.isRequired
};

export default ProductListStock;